@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

h1 {
  font-size: 40px;
  line-height: 52px;
}

@media (width >= 640px) {
  h1 {
    font-size: 48px;
    line-height: 62px;
  }
}

@media (width >= 1280px) {
  h1 {
    font-size: 64px;
    line-height: 78px;
  }
}

h2 {
  font-size: 30px;
  line-height: 40px;
}

@media (width >= 640px) {
  h2 {
    font-size: 36px;
    line-height: 46px;
  }
}

@media (width >= 1280px) {
  h2 {
    font-size: 56px;
    line-height: 70px;
  }
}

h3 {
  font-size: 18px;
  line-height: 22px;
}

@media (width >= 640px) {
  h3 {
    font-size: 24px;
    line-height: 30px;
  }
}

@media (width >= 1280px) {
  h3 {
    font-size: 34px;
    line-height: 42px;
  }
}

h4 {
  font-size: 22px;
  line-height: 28px;
}

h5 {
  margin: 0;
  font-size: 16px;
  line-height: 22px;
}

h6 {
  font-size: 14px;
  line-height: 20px;
}

h1, h2, h3, h4, h6 {
  text-align: left;
  text-transform: uppercase;
  margin: 0;
  font-weight: 700;
}

p {
  font-size: 16px;
  line-height: 24px;
}

.secondary {
  font-size: 14px;
  line-height: 21px;
}

.thirdary {
  text-transform: uppercase;
  font-size: 14px;
  line-height: 21px;
}

p, .secondary, .thirdary {
  text-align: left;
  margin: 0;
  font-weight: 400;
}

.section {
  box-sizing: border-box;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
  width: 100%;
  max-width: 1500px;
  padding-left: 30px;
  padding-right: 30px;
  display: grid;
}

@media (width >= 640px) {
  .section {
    grid-template-columns: repeat(8, 1fr);
    padding-left: 34px;
    padding-right: 34px;
  }
}

@media (width >= 1280px) {
  .section {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
    padding-left: 123px;
    padding-right: 123px;
  }
}

.button {
  text-align: left;
  text-transform: uppercase;
  color: #f7f7f7;
  cursor: pointer;
  background-color: #333;
  border: none;
  outline: none;
  place-content: center;
  width: 100%;
  height: 48px;
  font-size: 12px;
  font-weight: 700;
  line-height: 48px;
  text-decoration: none;
  transition: background-color .3s;
  display: flex;
}

.button:hover {
  background-color: #131313;
}

.header {
  position: relative;
}

.header__image_m {
  object-fit: cover;
  object-position: left;
  aspect-ratio: 5.1 / 3;
  width: 100%;
}

@media (width >= 640px) {
  .header__image_m {
    display: none;
  }
}

.header__title {
  grid-column: 1 / -1;
  padding-top: 40px;
}

@media (width >= 640px) {
  .header__title {
    grid-column: 1 / 5;
    padding-top: 100px;
  }
}

@media (width >= 1280px) {
  .header__title {
    padding-top: 140px;
  }
}

.header__description {
  grid-column: 1 / -1;
  margin-top: 20px;
}

@media (width >= 640px) {
  .header__description {
    grid-column: 1 / 4;
    margin-top: 30px;
  }
}

.header__image_t {
  object-fit: cover;
  object-position: left;
  aspect-ratio: 3.5 / 2;
  z-index: -1;
  width: 60%;
  max-width: 1178px;
  max-height: 387px;
  display: none;
  position: absolute;
  bottom: 0;
  right: 0;
}

@media (width >= 640px) {
  .header__image_t {
    display: flex;
  }
}

@media (width >= 1280px) {
  .header__image_t {
    aspect-ratio: unset;
    width: 60%;
    max-width: 1173px;
    max-height: 492px;
    padding-right: 80px;
  }
}

.menu {
  color: #333;
  pointer-events: none;
  opacity: 0;
  z-index: 3;
  background-color: #f7f7f7;
  flex-direction: column;
  width: 100%;
  transition: all 1s;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  overflow: auto;
  transform: translateX(100%);
}

.menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.menu__nav {
  flex-direction: column;
  gap: 25px;
  margin-top: 30px;
  display: flex;
}

@media (width >= 640px) {
  .menu__nav {
    gap: 40px;
    margin-top: 40px;
  }
}

@media (width >= 1280px) {
  .menu__nav {
    gap: 60px;
    margin-top: 70px;
  }
}

.menu__nav > a {
  color: #333;
  text-align: left;
  font-family: Inter, serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-decoration: none;
  transition: font-weight .3s;
}

@media (width >= 640px) {
  .menu__nav > a {
    font-size: 22px;
    line-height: 28px;
  }
}

.menu__nav > a:hover {
  font-weight: 700;
}

.menu__middlepart {
  gap: 20px;
  margin: 50px 0;
  font-size: 14px;
  line-height: 20px;
  display: flex;
}

@media (width >= 640px) {
  .menu__middlepart {
    display: none;
  }
}

.menu__middlepart > a {
  color: #333;
  text-decoration: none;
  transition: font-weight .3s;
}

.menu__middlepart > a:hover {
  font-weight: 700;
}

.menu__middlepart__languages {
  gap: 3px;
  display: flex;
}

.menu__middlepart__languages > p {
  font-size: 14px;
  line-height: 20px;
}

.menu__middlepart__languages__active {
  font-weight: 700;
}

.menu__button {
  margin-top: auto;
  margin-bottom: 30px;
}

.topbar {
  grid-column: 1 / -1;
  place-content: center space-between;
  align-items: center;
  padding: 25px 0;
  display: flex;
}

@media (width >= 640px) {
  .topbar {
    padding: 30px 0;
  }
}

.topbar__group {
  align-content: center;
  align-items: center;
  gap: 30px;
  line-height: 21px;
  display: flex;
}

@media (width >= 1280px) {
  .topbar__group {
    gap: 47px;
  }

  .topbar__group__menu {
    display: none;
  }
}

.topbar__group__link {
  cursor: pointer;
  color: #333;
  text-decoration: none;
  transition: transform .3s;
  display: none;
}

@media (width >= 640px) {
  .topbar__group__link {
    display: flex;
  }
}

.topbar__group__link:hover {
  transform: scale(1.3);
}

.topbar__group__languages {
  text-transform: uppercase;
  color: #828282;
  gap: 3px;
  font-weight: 300;
  display: none;
}

@media (width >= 640px) {
  .topbar__group__languages {
    display: flex;
  }
}

.topbar__group__languages__language {
  cursor: pointer;
  transition: transform .3s;
}

.topbar__group__languages__language:hover {
  transform: scale(1.3);
}

.topbar__group__languages__active {
  color: #333;
  font-weight: 400;
}

.topbar__group__button {
  cursor: pointer;
  width: 100px;
  display: none;
}

@media (width >= 640px) {
  .topbar__group__button {
    display: flex;
  }
}

.icon {
  box-sizing: border-box;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-origin: content-box;
  width: 32px;
  height: 32px;
  transition: transform .3s;
}

.icon:hover {
  transform: scale(1.3);
}

.icon--no-hover {
  transition: none;
}

.icon--no-hover:hover {
  transform: none;
}

.icon--logo {
  background-image: url("logo_icon.babc7ed5.png");
  width: 163px;
  height: 26px;
}

.icon--menu {
  background-image: url("menu_icon.9c366bcd.png");
  padding: 8px 4px;
}

.icon--close {
  background-image: url("cross_icon.99e09eba.png");
  width: 24px;
  height: 24px;
  padding: 4px;
}

.icon--spec-1 {
  background-image: url("spec_1_icon.748b2f3a.png");
  padding: 4px 10px;
}

.icon--spec-2 {
  background-image: url("spec_2_icon.fe952352.png");
  padding: 4px 12px;
}

.icon--spec-3 {
  background-image: url("spec_3_icon.28231ff8.png");
  padding: 6px 12px;
}

.icon--spec-4 {
  background-image: url("spec_4_icon.29531536.png");
  padding: 2px 13px;
}

.icon--spec-1, .icon--spec-2, .icon--spec-3, .icon--spec-4 {
  width: 70px;
  height: 50px;
}

.icon--arrow-up {
  background-image: url("arrow_up_icon.10b68c61.png");
  padding: 0 2px;
}

.icon--fb {
  background-image: url("fb_icon.008f1395.png");
  padding: 3px 6px;
}

.icon--tw {
  background-image: url("tw_icon.0a755baf.png");
  padding: 4px 1px;
}

.icon--ig {
  background-image: url("ig_icon.c275a492.png");
  padding: 3px;
}

.icon--arrow-up, .icon--fb, .icon--tw, .icon--ig {
  width: 24px;
  height: 24px;
}

.icon--features {
  background-image: url("features_icon.9eee5a31.png");
  width: 93px;
  height: 47px;
}

.specs {
  grid-template-columns: 1fr;
}

@media (width >= 640px) {
  .specs {
    grid-template-columns: 1fr 1fr;
    row-gap: 65px;
  }
}

@media (width >= 1280px) {
  .specs {
    grid-template-columns: repeat(4, 1fr);
    row-gap: 62px;
  }
}

.specs__menu {
  grid-column: 1 / -1;
  justify-content: center;
  gap: 84px;
  padding: 28px 0;
  display: none;
}

@media (width >= 1280px) {
  .specs__menu {
    display: flex;
  }
}

.specs__menu > a {
  color: #333;
  text-decoration: none;
  transition: all .3s;
}

.specs__menu > a:hover {
  font-weight: 700;
  transform: scale(1.2);
}

.specs__spec {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
  display: flex;
}

@media (width >= 1280px) {
  .specs__spec {
    gap: 24px;
  }
}

.specs__spec__title {
  text-align: center;
  margin-top: 20px;
  display: inline;
}

@media (width >= 640px) {
  .specs__spec__title {
    height: 56px;
    margin-top: 30px;
  }
}

@media (width >= 640px) and (width >= 728px) {
  .specs__spec__title {
    height: 28px;
  }
}

@media (width >= 1280px) {
  .specs__spec__title {
    height: 56px;
    margin-top: 6px;
  }
}

.specs__spec__description {
  text-align: center;
}

.presentation {
  max-width: 846px;
  padding: 0;
  display: flex;
}

.presentation__img {
  box-sizing: border-box;
  width: 100%;
  height: auto;
  padding: 0 12px 0 20px;
}

@media (width >= 640px) {
  .presentation__img {
    padding: 0 34px;
  }
}

@media (width >= 1280px) {
  .presentation__img {
    padding: 0;
  }
}

.about_us {
  flex-direction: column;
  gap: 110px;
  display: flex;
}

@media (width >= 640px) {
  .about_us {
    gap: 140px;
  }
}

@media (width >= 1280px) {
  .about_us {
    gap: 170px;
  }
}

@media (width >= 640px) {
  .about_us__card {
    padding: 0;
  }
}

@media (width >= 1280px) {
  .about_us__card {
    padding-left: 123px;
    padding-right: 123px;
  }
}

.about_us__card > h2 {
  grid-column: 1 / -1;
  margin-bottom: 30px;
}

.about_us__card__text {
  grid-column: 1 / -1;
}

@media (width >= 640px) {
  .about_us__card__text {
    padding-right: 34px;
  }
}

@media (width >= 1280px) {
  .about_us__card__text {
    padding-right: 0;
  }
}

.about_us__card__text > p {
  color: #7c7c7c;
  margin-top: 20px;
}

@media (width >= 640px) {
  .about_us__card__text > p {
    margin-top: 30px;
  }
}

.about_us__card__text > a {
  margin-top: 30px;
}

@media (width >= 640px) {
  .about_us__card__text > a {
    margin-top: 60px;
  }
}

.about_us__card__text__button {
  color: #39bebf;
  background-color: #0000;
  flex-direction: column;
  width: 127px;
  height: min-content;
  line-height: 20px;
  transition: all .3s;
}

.about_us__card__text__button:after {
  content: "";
  background-color: #39bebf;
  width: 50%;
  height: 1px;
  transition: all .3s;
}

.about_us__card__text__button:hover {
  color: #0c797a;
  background-color: #0000;
}

.about_us__card__text__button:hover:after {
  background-color: #0c797a;
  width: 100%;
}

.about_us__card__img {
  object-fit: cover;
  object-position: center;
  grid-column: 1 / -1;
  width: 100%;
  height: 210px;
}

@media (width >= 640px) {
  .about_us__card__img {
    height: 415px;
  }
}

@media (width >= 1280px) {
  .about_us__card__img {
    height: 388px;
  }
}

@media (width >= 640px) {
  .about_us__card--1 > img {
    grid-column: 1 / 5;
  }

  .about_us__card--1 > div {
    grid-column: 5 / -1;
  }
}

@media (width >= 1280px) {
  .about_us__card--1 > div {
    grid-column: 7 / -1;
  }

  .about_us__card--1 > img {
    grid-column: 1 / 7;
  }
}

@media (width >= 640px) {
  .about_us__card--2 > img {
    grid-column: 5 / -1;
  }

  .about_us__card--2 > div {
    grid-column: 1 / 5;
    padding-left: 34px;
    padding-right: 0;
  }
}

@media (width >= 1280px) {
  .about_us__card--2 > img {
    grid-column: 7 / -1;
  }

  .about_us__card--2 > div {
    grid-column: 1 / 7;
    padding-left: 0;
  }
}

.testimonilas {
  flex-direction: column;
  align-items: center;
  gap: 30px;
  display: flex;
}

@media (width >= 640px) {
  .testimonilas {
    gap: 40px;
  }
}

@media (width >= 1280px) {
  .testimonilas {
    gap: 30px;
  }
}

.testimonilas__quote {
  text-align: center;
  max-width: 786px;
  padding-top: 50px;
  font-size: 14px;
  font-weight: 700;
  line-height: 20px;
  position: relative;
}

@media (width >= 640px) {
  .testimonilas__quote {
    padding-top: 60px;
    font-size: 24px;
    line-height: 36px;
  }
}

@media (width >= 1280px) {
  .testimonilas__quote {
    padding-top: 42px;
    font-size: 24px;
    line-height: 36px;
  }
}

.testimonilas__quote__image {
  width: 208px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

@media (width >= 640px) {
  .testimonilas__quote__image {
    width: 233px;
  }
}

.testimonilas__credit {
  flex-direction: column;
  align-items: center;
  gap: 10px;
  display: flex;
}

@media (width >= 640px) {
  .testimonilas__credit {
    gap: 20px;
  }
}

.testimonilas__credit__avatar {
  object-fit: cover;
  border-radius: 50%;
  width: 62px;
  height: 62px;
}

.testimonilas__credit__text {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.features {
  flex-direction: column;
  align-items: center;
  gap: 30px;
  width: 100%;
  display: flex;
}

.features__content {
  width: 100%;
  padding: 0;
}

@media (width >= 640px) {
  .features__content {
    padding-left: 34px;
    padding-right: 34px;
  }
}

@media (width >= 1280px) {
  .features__content {
    padding-left: 123px;
    padding-right: 123px;
  }
}

.features__content__banner {
  background-color: #cfeff0;
  grid-column: 1 / -1;
  grid-template-columns: 1fr;
  padding: 30px;
  display: grid;
  position: relative;
}

@media (width >= 640px) {
  .features__content__banner {
    grid-template-columns: 1fr 1fr;
    padding: 60px 90px;
  }
}

@media (width >= 1280px) {
  .features__content__banner {
    column-gap: inherit;
    row-gap: 56px;
    padding: 60px 102px;
  }
}

.features__content__banner__group {
  grid-column: 1 / -1;
  display: grid;
}

@media (width >= 640px) {
  .features__content__banner__group {
    grid-column: unset;
  }
}

.features__content__banner__group > h3 {
  color: #0c797a;
}

.features__content__banner__group__icon {
  justify-self: center;
  margin-top: 50px;
  margin-bottom: 173px;
}

@media (width >= 640px) {
  .features__content__banner__group__icon {
    justify-self: unset;
    margin-bottom: 123px;
  }
}

@media (width >= 1280px) {
  .features__content__banner__group__icon {
    display: none;
  }
}

.features__content__banner__image {
  width: 320px;
  height: auto;
  position: absolute;
  bottom: 0;
  right: 0;
}

@media (width >= 640px) {
  .features__content__banner__image {
    width: 415px;
  }
}

@media (width >= 1280px) {
  .features__content__banner__image {
    width: 543px;
  }
}

.contact_us {
  row-gap: 27px;
}

.contact_us__title {
  grid-column: 1 / -1;
}

@media (width >= 640px) {
  .contact_us__title {
    grid-column: 1 / 5;
  }
}

@media (width >= 1280px) {
  .contact_us__title {
    grid-column: 1 / 7;
  }
}

.contact_us__form {
  flex-direction: column;
  grid-column: 1 / -1;
  gap: 20px;
  display: flex;
}

@media (width >= 640px) {
  .contact_us__form {
    grid-column: 5 / -1;
  }
}

@media (width >= 1280px) {
  .contact_us__form {
    grid-column: 7 / -1;
  }
}

.contact_us__form__input {
  box-sizing: border-box;
  color: #333;
  transition: all .3px;
  background-color: #0000;
  border: 1px solid #828282;
  outline: none;
  height: 48px;
  padding: 12px 30px;
  font-size: 16px;
  line-height: 24px;
  transition: border .3s;
}

.contact_us__form__input:hover {
  border: 1px solid #39bebf;
}

.contact_us__form__input:focus {
  border: 1px solid #0c797a;
}

.contact_us__form__input::placeholder {
  color: #bdbdbd;
}

.contact_us__form__textarea {
  resize: none;
  height: 128px;
  font-family: inherit;
}

@media (width >= 640px) {
  .contact_us__form__textarea {
    height: 200px;
  }
}

@media (width >= 1280px) {
  .contact_us__form__textarea {
    height: 128px;
  }
}

.contact_us__form__button {
  background-color: #39bebf;
  width: 123px;
  margin-top: 10px;
  transition: background-color .3s;
}

.contact_us__form__button:hover {
  background-color: #0c797a;
}

.footer {
  color: #bdbdbd;
  flex-direction: column;
  grid-column: 1 / -1;
  align-items: center;
  gap: 30px;
  margin-bottom: 30px;
  display: flex;
  position: relative;
}

@media (width >= 640px) {
  .footer {
    gap: 60px;
  }
}

.footer__icons {
  gap: 40px;
  display: flex;
}

.footer > p {
  text-align: center;
}

.footer__up {
  position: absolute;
  top: 0;
  right: 30px;
}

@media (width >= 640px) {
  .footer__up {
    right: 34px;
  }
}

@media (width >= 1280px) {
  .footer__up {
    right: 123px;
  }
}

html {
  scroll-behavior: smooth;
  scrollbar-width: none;
  overflow-x: hidden;
}

html:has(.menu:target) {
  overflow: hidden;
}

body {
  box-sizing: border-box;
  color: #333;
  background-color: #f7f7f7;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 110px;
  width: 100vw;
  min-width: 320px;
  margin: 0;
  font-family: Inter, serif;
  display: flex;
}

@media (width >= 640px) {
  body {
    gap: 140px;
  }
}

@media (width >= 1280px) {
  body {
    gap: 170px;
  }
}

ul {
  flex-direction: column;
  gap: 10px;
  margin-bottom: 0;
  padding: 0 0 0 15px;
  display: flex;
}

li {
  font-size: 14px;
  line-height: 21px;
}

@media (width >= 1280px) {
  li {
    font-size: 16px;
    line-height: 24px;
  }
}

